      </div>
      <!-- üîΩ Í∏∞Ï°¥ ÏßÄÏõêÍ≥ÑÌöç Î™©Î°ù (Î™®Îì† Ïó≠Ìï†Ïö©, ÏùºÎ∞òÏù¥ ÏïÑÎãå Í≤ΩÏö∞ÏóêÎßå ÌïÑÌÑ∞) -->
      <div class="section-block section-block--plans">
        <!-- üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ (ÏùºÎ∞ò Ïù¥Ïö©Ïûê Ï†úÏô∏) -->
        <div v-if="selectedRole !== 1" class="filter-row mt-3">
          <form class="filter-form" @submit.prevent="onSearch">
            <!-- Í≤ÄÏÉâ Ïù∏Ìíã -->
