              >
                <option value="ALL">Ï†ÑÏ≤¥ ÏÉÅÌÉú</option>
                <option value="REVIEW">Í≤ÄÌÜ†Ï§ë</option>
                <option value="PROGRESS">ÏßÑÌñâÏ§ë</option>
                <option value="DONE">ÏßÄÏõêÏôÑÎ£å</option>
                <option value="RESUBMIT">Ïû¨ÏäπÏù∏ÏöîÏ≤≠</option>
                <option value="REJECT">Î∞òÎ†§</option>
              </select>
            </div>

            <!-- Ï†ïÎ†¨ ÏÖÄÎ†âÌä∏ -->
            <div class="filter-field filter-field--select select-wrapper">
              <select
                v-model="sortOption"
                class="select-input"
                @change="onFilterChange"
              >
                <option value="WRITTEN_RECENT">Í≥ÑÌöç ÏûëÏÑ±Ïùº ÏµúÏã†Ïàú</option>
                <option value="WRITTEN_OLD">Í≥ÑÌöç ÏûëÏÑ±Ïùº Ïò§ÎûòÎêúÏàú</option>
                <option value="SUBMIT_RECENT">Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂úÏùº ÏµúÏã†Ïàú</option>
                <option value="SUBMIT_OLD">Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂úÏùº Ïò§ÎûòÎêúÏàú</option>
                <option value="NAME">Ïù¥Î¶ÑÏàú</option>
              </select>
            </div>

            <!-- Í≤ÄÏÉâ Î≤ÑÌäº -->
            <div class="filter-field filter-field--button">
              <MaterialButton type="submit" color="dark" size="sm">
                Í≤ÄÏÉâ
              </MaterialButton>
